import { mapState } from "vuex";
export default {
  name: "now",
  data() {
    return {
      apiUrl: "",
      token: "",
      user: "",
      repo: "",
      gitType: "github"
    };
  },
  computed: {
    ...mapState({
      settings: state => {
        return state.settings;
      }
    })
  },
  mounted() {
    this.apiUrl = this.settings.apiUrl;
    this.token = this.settings.token;
    this.user = this.settings.user;
    this.repo = this.settings.repo;
    this.gitType = this.settings.gitType;
  },
  methods: {
    saveSettings() {
      const settings = {
        token: this.token,
        apiUrl: this.apiUrl,
        user: this.user,
        repo: this.repo,
        gitType: this.gitType
      };
      this.$q.localStorage.set("lee-todo-settings", settings);
      this.$store.commit("initSettings", { settings: settings });
      this.$store.dispatch("init");
      this.$router.push({
        path: "/now"
      });
    }
  }
};
